/* Settings Actions - Modern Design */

.settings-actions-panel {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 20px 0;
  padding: 0 20px;
}

.settings-actions-btns {
  display: flex;
  gap: 16px;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  max-width: 500px;
  width: 100%;
}

.settings-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 50%, #f093fb 100%);
  color: white;
  border: none;
  border-radius: 16px;
  padding: 14px 24px;
  font-size: 16px;
  font-weight: 700;
  box-shadow: 0 4px 20px rgba(102, 126, 234, 0.3);
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  outline: none;
  min-width: 160px;
  position: relative;
  overflow: hidden;
}

.settings-btn::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.2), transparent);
  transition: left 0.5s;
}

.settings-btn:hover::before {
  left: 100%;
}

.settings-btn:hover {
  transform: translateY(-2px) scale(1.05);
  box-shadow: 0 8px 30px rgba(102, 126, 234, 0.4);
  background: linear-gradient(135deg, #f093fb 0%, #764ba2 50%, #667eea 100%);
}

.settings-btn:active {
  transform: translateY(0) scale(0.98);
  box-shadow: 0 2px 10px rgba(102, 126, 234, 0.3);
}

.settings-btn-icon {
  font-size: 18px;
  filter: drop-shadow(0 1px 2px rgba(0, 0, 0, 0.2));
}

.settings-btn-logout {
  background: linear-gradient(135deg, #ff6b6b 0%, #ee5a52 50%, #ff4757 100%);
  box-shadow: 0 4px 20px rgba(255, 107, 107, 0.3);
}

.settings-btn-logout:hover {
  background: linear-gradient(135deg, #ff4757 0%, #ee5a52 50%, #ff6b6b 100%);
  box-shadow: 0 8px 30px rgba(255, 107, 107, 0.4);
}

.settings-btn-logout:active {
  box-shadow: 0 2px 10px rgba(255, 107, 107, 0.3);
}

.settings-actions-footer {
  margin-top: 16px;
  font-size: 14px;
  color: #666;
  text-align: center;
  opacity: 0.8;
}

.settings-footer-link {
  color: #667eea;
  text-decoration: none;
  font-weight: 600;
  transition: color 0.3s;
}

.settings-footer-link:hover {
  color: #764ba2;
  text-decoration: underline;
}

.settings-footer-sep {
  color: #999;
  margin: 0 8px;
}

.settings-footer-version {
  color: #888;
  font-size: 12px;
}

/* Responsive Design */
@media (max-width: 768px) {
  .settings-actions-panel {
    margin: 16px 0;
    padding: 0 16px;
  }
  
  .settings-actions-btns {
    gap: 12px;
    flex-direction: column;
    width: 100%;
  }
  
  .settings-btn {
    width: 100%;
    max-width: 280px;
    padding: 16px 20px;
    font-size: 15px;
  }
  
  .settings-btn-icon {
    font-size: 16px;
  }
}

@media (max-width: 480px) {
  .settings-actions-panel {
    padding: 0 12px;
  }
  
  .settings-btn {
    padding: 14px 18px;
    font-size: 14px;
    min-width: 140px;
  }
  
  .settings-btn-icon {
    font-size: 15px;
  }
}

/* Dark Theme Support */
[data-theme="dark"] .settings-btn {
  background: linear-gradient(135deg, #4b5563 0%, #374151 50%, #1f2937 100%);
  box-shadow: 0 4px 20px rgba(75, 85, 99, 0.3);
}

[data-theme="dark"] .settings-btn:hover {
  background: linear-gradient(135deg, #1f2937 0%, #374151 50%, #4b5563 100%);
  box-shadow: 0 8px 30px rgba(75, 85, 99, 0.4);
}

[data-theme="dark"] .settings-btn-logout {
  background: linear-gradient(135deg, #dc2626 0%, #b91c1c 50%, #991b1b 100%);
  box-shadow: 0 4px 20px rgba(220, 38, 38, 0.3);
}

[data-theme="dark"] .settings-btn-logout:hover {
  background: linear-gradient(135deg, #991b1b 0%, #b91c1c 50%, #dc2626 100%);
  box-shadow: 0 8px 30px rgba(220, 38, 38, 0.4);
}

[data-theme="dark"] .settings-actions-footer {
  color: #d1d5db;
}

[data-theme="dark"] .settings-footer-link {
  color: #8b5cf6;
}

[data-theme="dark"] .settings-footer-link:hover {
  color: #a78bfa;
} 